"use client";
import { useDispatch, useSelector } from "react-redux";
import {
  IconOverview, IconBalances, IconTransactions, IconBills,
  IconExpenses, IconGoals, IconSettings, IconLogout, IconMenu, IconDots, IconClose
} from "./Icons"; // ðŸ‘ˆ IconClose bhi import karo
import MenuItem from "./MenuItem";
import { toggleSidebar, closeSidebar } from "@/store/slices/uiSlice";
import Image from "next/image";

const MENU = [
  { label: "Overview", icon: <IconOverview /> },
  { label: "Balances", icon: <IconBalances /> },
  { label: "Transactions", icon: <IconTransactions /> },
  { label: "Bills", icon: <IconBills /> },
  { label: "Expenses", icon: <IconExpenses /> },
  { label: "Goals", icon: <IconGoals /> },
  { label: "Settings", icon: <IconSettings /> },
];

export default function Sidebar() {
  const dispatch = useDispatch();
  const { sidebarOpen } = useSelector((s) => s.ui);

  return (
    <>
      {/* Mobile hamburger button */}
      <button
        onClick={() => dispatch(toggleSidebar())}
        className="lg:hidden fixed top-3 left-3 z-50 bg-white text-gray-900 rounded-xl shadow-md p-2"
      >
        <IconMenu />
      </button>

      {/* Overlay for mobile */}
      {sidebarOpen && (
        <div
          className="lg:hidden fixed inset-0 bg-black/40 z-40"
          onClick={() => dispatch(closeSidebar())}
        />
      )}

      <aside
        className={`fixed lg:static inset-y-0 left-0 z-50 w-64 bg-[#0f141a] text-slate-300 border-r border-[#1f2937]
        grid grid-rows-[auto_1fr_auto] p-5 transform transition-transform duration-300
        ${sidebarOpen ? "translate-x-0" : "-translate-x-full lg:translate-x-0"}
      `}
      >
        {/* ðŸ‘‡ Yaha close button sirf mobile pe dikhayenge */}
        <div className="flex items-center justify-between mb-4 lg:hidden">
          <div className="flex items-center gap-3">
            <div className="w-3.5 h-3.5 rounded bg-gradient-to-br from-teal-500 to-sky-500 ring-4 ring-[#0f141a]" />
            <span className="text-lg font-bold tracking-wide">XpenseTrakr</span>
          </div>
          <button
            onClick={() => dispatch(closeSidebar())}
            className="text-slate-300 hover:text-white"
          >
            <IconClose />
          </button>
        </div>

        {/* ðŸ‘‡ Desktop brand header (close button nahi chahiye) */}
        <div className="hidden lg:flex items-center gap-3 mb-4">
          <div className="w-3.5 h-3.5 rounded bg-gradient-to-br from-teal-500 to-sky-500 ring-4 ring-[#0f141a]" />
          <span className="text-lg font-bold tracking-wide">XpenseTrakr</span>
        </div>

        {/* Menu */}
        <nav className="flex flex-col gap-2" aria-label="Main menu">
          {MENU.map((m) => (
            <MenuItem key={m.label} label={m.label} icon={m.icon} />
          ))}
        </nav>

        {/* Bottom section */}
        <div className="mt-4 flex flex-col gap-3">
          <button className="flex items-center gap-2 bg-[#0e1319] border border-[#1f2937] text-slate-200 px-3 py-2 rounded-xl">
            <span className="text-slate-400"><IconLogout /></span>
            Logout
          </button>

          <div className="flex items-center gap-3 bg-[#161b22] border border-[#1f2937] rounded-xl p-2">
            <div className="w-9 h-9 overflow-hidden rounded-full">
              <Image
                src="https://api.dicebear.com/7.x/initials/svg?seed=Sarah%20William"
                alt="Sarah William"
                width={36}
                height={36}
              />
            </div>
            <div className="flex flex-col leading-tight">
              <span className="text-sm font-semibold text-white">Sarah William</span>
              <a href="#" className="text-xs text-slate-400 hover:underline">
                View profile
              </a>
            </div>
            <button className="ml-auto text-slate-400">
              <IconDots />
            </button>
          </div>
        </div>
      </aside>
    </>
  );
}
